@import 'variables';
@import url('https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700&display=swap');
@import 'mixins';
@import 'xclasses';
@import 'header';
@import 'footer';
@import 'sign-up';
@import 'sign-in';
@import '404';
@import 'styles';
@import 'buy';
@import 'succes';
@import 'account';

body {
	margin: 0;
	font-family: 'Roboto', sans-serif;	
}

.container {
	box-sizing: border-box;
	max-width: 1035px;
	padding-top: 35px;
	padding-left: 65px;
	padding-right: 65px;
	margin-left: auto;
	margin-right: auto;
	
	&__index {
		background-image: url('../images/background-header.png');
		background-repeat: no-repeat;
	}
}

@media (max-width: 1042px) {
	.container {
		padding-left: 20px;
		padding-right: 20px;
	}
}

@media (max-width: 399px) {
	.container {
		padding-top: 20px;
		padding-left: 10px;
		padding-right: 10px;

		&__index {
			background-position-x: -95px;
			background-position-y: -203px;
		}
	}
}

@media (max-width: 345px) {
	.container {

		&__index {
			background-position-x: -95px;
			background-position-y: -172px;
		}
	}
}

.main {

	&__title {
		@include title;
		font-size: $fs_XXL;
		line-height: 40px;

		&--style {
			max-width: 741px;
			margin-bottom: 15px;
			text-transform: uppercase;			
		}
	}

	&__description {
		@include text;
		margin-bottom: 45px;
		max-width: 544px;
		font-size: $fs-M;
		line-height: 30px;
	}

	&__button {
		@include button-link;
		padding: 6px 60px;
		font-size: $fs_L;
	}
}

@media (max-width: 399px) {
	.main {

		&__title {

			&--style {
				font-size: 32px;
				max-width: 346px;
				width: 100%;
			}			
		}

		&__description {
			font-weight: 500;

			&--del {
				display: none;
			}
		}
	}
}

@media (max-width: 397px) {
	.main {
		&__button {
			position: relative;
			top: 125px;
		}
	}	
}

@media (max-width: 370px) {
    .main {

        &__title {
            font-size: $fs_XL;
        }
    }
}

.section {
	position: relative;

	&:first-child {
		padding-top: 20px;
	}

	&__info {
		padding-top: 50px;
	}

	&__price {
		padding-top: 65px;
		padding-bottom: 35px;
	}

	&__img {
		display: block;
		width: 100%;
		max-width: 398px;		
		position: relative;
		bottom: 10px;
		left: 500px;
	}

	&__title {
		@include title;
		max-width: 650px;
		margin-bottom: 40px;
		font-size: $fs_XXL;
		text-transform: uppercase;
		line-height: 40px;
	}
}

@media (max-width: 987px) {
		
	.section {

		&__img {
			position: static;
			margin-top: 25px;
			margin-left: auto;
		}
	}
}

@media (max-width: 905px) {
		
	.section {

		&__img {
			margin-top: 70px;
		}
	}
}

@media (max-width: 869px) {
	.section {

		&__img {
			margin-top: 85px;
		}
	}
}

@media (max-width: 765px) {
	.section {

		&__img {
			max-width: 215px;
			width: 100%;
			margin-top: 115px;
		}
	}
}

@media (max-width: 639px) {
	.section {

		&__img {
			margin-top: 155px;
		}
	}
}

@media (max-width: 463px) {
	.section {

		&__img {
			margin-top: 120px;
		}
	}
}

@media (max-width: 451px) {
	.section {

		&__img {
			margin-top: 100px;
		}
	}
}

@media (max-width: 397px)  {
	.section {

		&__title {
			font-size: $fs_XL;
			font-weight: 500;
		}

		&__img {
			margin-top: -45px;
		}		
	}
}

.info {

	&__boxes {
		display: flex;
		justify-content: space-between;
	}

	&__box {
		position: relative;
		box-sizing: border-box;
		width: 280px;
		height: 240px;		
		background-image: url('../images/cloud1.png');
		background-repeat: no-repeat;

		&:first-child {
			padding-left: 30px;
			padding-right: 35px;

			& .box__img {
				padding-top: 12px;
				margin-left: 75px;				
			}

			& .box__title {
				top: 40px;
				left: 40px;
			}
		}

		&:nth-child(2) {
			padding-left: 50px;
			padding-right: 50px;

			& .box__img {
				padding-top: 20px;
				margin-left: 10px;				
			}

			& .box__title {
				top: 40px;
				left: 40px;
			}
		}

		&:last-child {
			padding-left: 50px;
			padding-right: 35px;

			& .box__img {
				padding-top: 60px;				
			}

			& .box__title {
				top: 40px;
				left: 40px;
			}
		}
	}

	&__img {
		display: block;
		margin-left: auto;
		margin-right: auto;

		&:nth-child(4) {
			display: none;
		}

		&:nth-child(5) {
			max-width: 715px;
			width: 100%;
		}
	}
}

@media (max-width: 987px) {
	.info {

		&__boxes {
			flex-wrap: wrap;
			justify-content: center;

		}

		&__box {

			&:nth-child(2n) {
				margin-left: 60px;
			}
		}

		&__img {
			&:nth-child(3) {
				display: none;
			}

			&:nth-child(4) {
				display: block;
				transform: scale(-1, 1);
			}
		}
	}
}

@media (max-width: 660px) {
	.info {

		&__box {

			&:nth-child(2) {
				margin-left: 0;
			}

			&:nth-child(-n+2) {
				margin-bottom: 10px;
			}			
		}

		&__img {

			&:nth-child(4) {
				position: relative;
				transform: scaleX(1);
				top: -10px;
				left: -75px;
			}
		}
	}
}

.box {

	&__title {
		@include title;
		margin-bottom: 0;
		position: absolute;
		font-size: $fs-M;
	}

	&__description {
		@include text;
		margin-bottom: 0;
		font-size: $fs-M;
		line-height: 26px;
	}

	&__img {
		display: block;		
	}
}

.price {

	&__boxes {
		display: flex;
		justify-content: center;
	}

	&__box {
		display: flex;
		flex-wrap: wrap;
		align-content: flex-start;
		justify-content: center;
		position: relative;
		box-sizing: border-box;
		width: 285px;
		height: 295px;
		padding-top: 45px;
		background-image: url('../images/cloud2.png');
		background-repeat: no-repeat;

		&:not(:nth-child(2)) {
			top: 130px;
		}

		&:first-child .price__button {
			margin-top: 15px;
		}

		&:nth-child(2) .price__button {
			margin-top: 50px;
		}

		&:last-child .price__button {
			margin-top: 15px;
		}
	}

	&__title {
		@include title;
		margin-bottom: 15px;
		font-size: $fs_XL;
		font-weight: 500;
	}

	&__list {
		@include list;
	}

	&__item {
		position: relative;
		font-size: $fs_S;
		font-weight: 300;

		&:not(:last-child) {
			margin-bottom: 10px;
		}

		&::before {
			content: '';
			position: absolute;
			width: 7px;
			height: 7px;
			top: 8px;
			left: -10px;
			background-color: #000;
			border-radius: 50%;
		}
	}

	&__bill {
		@include text;
		position: absolute;
		margin-bottom: 0;
		top: 155px;
		right: 45px;
		font-size: $fs_XL;
		font-weight: 500;
	}

	&__button {
		@include button-link;
		display: block;
		width: 130px;
		padding-top: 6px;
		padding-bottom: 10px;
		font-size: $fs_L;
		font-weight: 500;
		text-align: center;
	}

	&__img {
		display: block;
		margin-left: auto;
		margin-right: auto;

		&:nth-child(3) {
			max-width: 208px;
			width: 100%;
			margin-top: 40px;
		}

		&:nth-child(4) {
			max-width: 597px;
			width: 100%;
		}
	}
}

@media (max-width: 895px) {
	.price {
		&__boxes {
			flex-wrap: wrap;
		}

		&__box {
			&:not(:nth-child(2)) {
				top: 0;
			}

			&:nth-child(2) {
				order: 1;
			}

			&:nth-child(3) {
				margin-left: 60px;
			}
		}
	}
}

@media (max-width: 667px) {
	.price {

		&__box {

			&:nth-child(2) {
				order: -1;
			}

			&:nth-child(3) {
				margin-left: 0;
			}

			&:nth-child(-n+2) {
				margin-bottom: 10px;
			}
		}
	}
}

@media (max-width: 383px) {
	.price {

		&__img {

			&:nth-child(3) {
				max-width: 180px;
				width: 100%;
				margin-top: 10px;
			}
		}
	}
}
