.main {

    &__succes {
        padding-top: 15px;
    }
}

.succes {

    &__block {
        position: relative;
        box-sizing: border-box;
        max-width: 635px;
        background-image: url('../images/succes-cloud.png');
        background-repeat: no-repeat;        
        padding-top: 125px;
        padding-left: 15px;
        padding-right: 15px;
    }

    &__text {
        @include text;        
        font-size: $fs_XL;
        font-weight: 500;
        text-align: center;

        &:first-child {
            margin-bottom: 30px;
        }

        &:nth-child(2) {
            margin-bottom: 0;
        }
    }

    &__img {
        display: block;
        max-width: 370px;
        width: 100%;
        position: relative;
        bottom: -8px;
        right: -575px;
        transform: rotate(-5deg);
    }
}

@media (max-width: 992px) {

    .succes {

        &__img {
            max-width: 300px;
        }
    }
}

@media (max-width: 924px) {
    .main {

        &__succes {
            background-position: center;
        }
    }

    .succes {

        &__block {
            max-width: 100%;
            background-position-x: center;
        }

        &__img {
            margin-top: 95px;
            max-width: 370px;
            right: auto;
            margin-left: auto;
            margin-right: auto;
        }
    }
}

@media (max-width: 675px) {
    .main {

        &__succes {
            background-position: center;
        }
    }

    .succes {

        &__block {
            background-size: contain;
        }        
    }
}

@media (max-width: 620px) {
    .succes {
        
        &__block {
            padding-top: 110px;
        }

        &__text {
           font-size: $fs_L; 
        }
    }    
}

@media (max-width: 575px) {
    .succes {
        
        &__block {
            padding-top: 100px;
        }

        &__text {
           font-size: $fs_L; 
        }
    }    
}

@media (max-width: 500px) {
    .succes {
        
        &__block {
            padding-top: 90px;
        }

        &__text {
           font-size: $fs_M; 
        }
    }    
}

@media (max-width: 461px) {
    .succes {
        
        &__block {
            padding-top: 80px;
        }

        &__text {
           font-size: $fs_S; 

           &:first-child {
                margin-bottom: 10px;
            }
        }
    }    
}

@media (max-width: 357px) {
    .succes {
        
        &__block {
            padding-top: 30px;
        }
    }    
}