.main {

    &__buy {
        display: flex;
        flex-wrap: wrap;
        align-items: flex-end;
        padding-top: 30px;
    }

    &__title-buy {
        margin-bottom: 0;
    }
}

@media (max-width: 839px) {
    .main {

        &__buy {
            justify-content: center;
            padding-top: 10px;
        }
    }    
}

.buy {

    &__section {
        max-width: 600px;
        width: 100%;
        background-image: url('../images/buy-cloud.png');
        background-repeat: no-repeat;
        background-position: center;
    }

    &__form {
        padding: 70px 75px 55px 75px;
    }

    &__title {
        @include title;
        font-size: $fs_XL;
        font-weight: 500;

        &:first-child {
            margin-bottom: 0;
        }

        &:nth-child(3) {
            margin-bottom: 15px;
        }
        
    }

    &__payment {
        padding-top: 20px;
        padding-bottom: 20px;
    }

    &__label-radio {
        position: relative;
        display: block;
        padding-left: 20px;
        font-size: $fs_S;
        font-weight: normal;

        &:nth-child(2) {
            margin-bottom: 20px;
        }
        
        &::before {
            content: '';
            display: inline-block;
            position: relative;
            width: 24px;
            height: 24px;
            top: 5px;
            left: -20px;
            box-sizing: border-box;
            border: 1px solid #000;
            border-radius: 50%;
            background-color: transparent;
        }

        &::after {
            content: '';
            display: none;
            position: absolute;
            width: 16px;
            height: 16px;
            top: 9px;
            left: 4px;
            box-sizing: border-box;
            border: 1px solid #000;
            border-radius: 50%;
            background-color: #000;
        }


    }

    &__input-radio {
        display: none;

        &:checked + .buy__label-radio::after {
            display: block;
        }
    }

    &__card {
        box-sizing: border-box;
        padding: 25px 45px 25px 30px;
        border: 4px solid #000;
        border-radius: 20px;
        margin-bottom: 20px;
    }

    &__button {
        @include button-link;
        display: block;
        max-width: 125px;
        width: 100%;
        font-size: $fs_L;
        font-weight: 500;
        padding: 7px 0;
        margin-left: auto;
        margin-right: auto;
    }

    &__img {
        display: block;
        max-width: 310px;
        width: 100%;
    }
}

@media (max-width: 950px) {
    .buy {

        &__img {
            max-width: 200px;
            width: 100%;
        }
    }
}

@media (max-width: 839px) {
    .buy {

        &__section {
            background-image: none;            
        }

        &__container {
            background-color: #FFB800;
        }

        &__form {
            padding: 25px 35px;
        }

        &__img {
            display: none;
        }
    }
}

@media (max-width: 470px) {

    .buy {

        &__form {
            padding: 25px 0;
        }
    }    
}

.card {

    &__wrapper {

        &-secure {
            display: flex;
            justify-content: space-between;
            padding-top: 10px;
        }

        &-expdate {

            &> .card__label {
                position: relative;

                &::after {
                    content: '/';
                    position: absolute;
                    display: block;
                    width: 12px;
                    height: 23px;
                    top: 32px;
                    left: 66px;
                    font-size: $fs-M;
                    font-weight: 500;
                    color: #000;
                }
            }
        }
    }

    &__input {
        @include input;

        &--cardholder {
            font-weight: 500;
            margin-bottom: 10px;
        }

        &--cardnumber {
            max-width: 295px;
            padding-left: 0;
            text-align: center;
        }

        &--expdate {
            max-width: 60px;
            padding-left: 0;
            text-align: center;

            &:nth-child(2) {
                margin-right: 15px;                
            }
        }

        &--securecode {
            max-width: 85px;
            padding-left: 0;
            text-align: center;
        }
    }

    &__label {
        display: block;
        @include label;
    }
}

@media (max-width: 370px) {
    .card {

        &__wrapper {

            &-secure {
                display: block;
            }

            &-cvv {
                padding-top: 10px;
            }
        }
    }
}