.main {
    padding-top: 70px;    

    &__sign-in-wrapper {
        max-width: 900px;
        width: 100%;
        margin-left: auto;
        margin-right: auto;
        padding-top: 90px;
        background-image: url('../images/sign-in-cloud.png');
        background-repeat: no-repeat;
        background-size: contain;
    }

    &__title {

        &--page-style {
            margin-bottom: 20px;
            text-align: center;
        }
    }
}

@media (max-width: 928px) {
    .main {
        
        &__sign-in-wrapper {
            padding-top: 10px;
            background-image: none;
        }
    }
}

.sign-in {

    &__form {
        display: flex;
        flex-wrap: wrap;
        max-width: 352px;
        width: 100%;
        margin-left: auto;
        margin-right: auto;
    }
    
    &__error {
        margin-bottom: 7px;
        color: #F84F07;
        font-size: $fs_S;
        font-weight: 300;
        opacity: 0;
    }

    &__input {
        @include input;
        position: relative;

        &:nth-child(5) {
            margin-bottom: 25px;
        }

        &:invalid:not(:placeholder-shown) + .sign-in__error {
            opacity: 1;
        }
    }

    &__checkbox {
        display: none;
    }

    &__label {
        font-size: $fs_S;
        line-height: 21px;
        font-weight: 300;
        color: #000;

        &-checkbox {
            position: relative;
            max-width: 252px;
            width: 100%;
            margin-left: 33px;
            margin-bottom: 15px;

            &::before {
                content: '';
                display: block;
                position: absolute;
                box-sizing: border-box;
                width: 23px;
                height: 23px;
                border: 2px solid #000;
                top: -5px;
                left: -33px;
            }

            &::after {
                content: '';
                display: none;
                position: absolute;
                width: 14px;
                height: 14px;
                top: 0;
                left: -28px;
                background-image: url('../images/check.png');
                background-repeat: no-repeat;
                background-position: center;
            }
        }
    }

    &__checkbox {

        &:checked + .sign-in__label-checkbox::after {
            display: block;
        }
    }

    &__restore-pass {
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 15px;
        font-size: $fs_S;
        font-weight: 300;
        color: #000;        
    }

    &__action-mobile {
        display: none;
        
    }

    &__action-text {
        @include text;
        font-size: $fs_S;
        font-weight: 300;
        margin-bottom: 20px;
        margin-left: auto;
        margin-right: auto;
    }

    &__button {
        @include button-link;
        width: 100%;
        padding-top: 7px;
        padding-bottom: 7px;
        margin-bottom: 30px;
        font-size: $fs_L;
        font-weight: 500;
    }

    &__img {
        display: block;
        position: relative;
        bottom: -3px;
        margin-left: auto;
        margin-right: auto;
    }

    &__alternative {
        display: flex;
        flex-wrap: wrap;
        max-width: 352px;
        width: 100%;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 135px;
    }

    &__social {

        &-link {
            @include link;
        }

        &-alternative {            
            padding-left: 0;
            margin-top: 0;
            margin-bottom: 0;
            margin-left: auto;
            margin-right: auto;
        }

        &:nth-child(3) {
            margin-right: 13px;
        }

        &-text {
            @include text;
            margin-bottom: 25px;
            font-size: $fs_S;
            font-weight: 300;
            margin-left: auto;
            margin-right: auto;
        }
    }
}

@media (max-width: 928px) {
    .sign-in {

        &__container {
            background-color: #FFB800;
        }

        &__button {
            margin-bottom: 65px;
        }

        &__alternative {
            margin-bottom: 0;
        }

        &__img {
            display: none;
        }

        &__social {

            &-alternative {
                margin-bottom: 40px;
            }            
        }
    }
}

@media (max-width: 709px) {
    .sign-in {

        
    }
}