.main {    

    &__wrapper {
        max-width: 900px;
        width: 100%;
        margin-left: auto;
        margin-right: auto;
        padding-top: 70px;
        background-image: url('../images/signup-cloud.png');
        background-repeat: no-repeat;
        background-size: contain;
    }

    &__title {

        &--page-style {
            margin-bottom: 20px;
            text-align: center;
        }
    }
}

@media (max-width: 928px) {
    .main {
        
        &__wrapper {
            padding-top: 10px;
            background-image: none;
        }
    }
}

.sign-up {

    &__form {
        display: flex;
        flex-wrap: wrap;
        max-width: 352px;
        width: 100%;
        margin-left: auto;
        margin-right: auto;
    }
    
    &__error {
        margin-bottom: 7px;
        color: #F84F07;
        font-size: $fs_S;
        font-weight: 300;
        opacity: 0;        
    }

    &__input {
        position: relative;
        box-sizing: border-box;
        width: 100%;
        border: 2px solid #000;
        background-color: transparent;
        padding-left: 20px;
        padding-top: 8px;
        padding-bottom: 8px;
        font-size: $fs-M;
        font-family: 'Roboto', sans-serif;

        &:focus {
            box-shadow: inset 1px 1px 0px 1px #000, inset -1px -1px 0px 1px #000;
            outline-color: #000;
        }

        &:nth-child(-2n+15) {
            margin-bottom: 25px;
        }

        &:nth-child(10) {
            margin-bottom: 40px;
        }

        &:invalid:not(:placeholder-shown) {
            border-color: #F84F07;
            box-shadow: inset 1px 1px 0px 1px #F84F07, inset -1px -1px 0px 1px #F84F07;
            outline-color: #F84F07;
        }

        &:invalid:not(:placeholder-shown) + .sign-up__error {
            opacity: 1;
        }
    }

    &__checkbox {
        display: none;
    }

    &__label {
        font-size: $fs_S;
        line-height: 21px;
        font-weight: 300;
        color: #000;

        &-checkbox {
            position: relative;
            max-width: 252px;
            width: 100%;
            margin-left: auto;
            margin-right: auto;
            margin-bottom: 30px;

            &::before {
                content: '';
                display: block;
                position: absolute;
                box-sizing: border-box;
                width: 23px;
                height: 23px;
                border: 2px solid #000;
                top: 4px;
                left: -33px;
            }

            &::after {
                content: '';
                display: none;
                position: absolute;
                width: 14px;
                height: 14px;
                top: 8px;
                left: -28px;
                background-image: url('../images/check.png');
                background-repeat: no-repeat;
                background-position: center;
            }
        }
    }

    &__checkbox {

        &:checked + .sign-up__label-checkbox::after {
            display: block;
        }
    }

    &__action-text {
        @include text;
        font-size: $fs_S;
        font-weight: 300;
        margin-bottom: 30px;
        margin-left: auto;
        margin-right: auto;
    }

    &__button {
        @include button-link;
        width: 100%;
        padding-top: 7px;
        padding-bottom: 7px;
        margin-bottom: 10px;
        font-size: $fs_L;
        font-weight: 500;
    }

    &__img {
        display: block;
        margin-left: auto;
        margin-right: 50px;
    }
}

@media (max-width: 928px) {
    .sign-up {

        &__container {
            background-color: #FFB800;
        }

        &__button {
            margin-bottom: 65px;
        }

        &__img {
            display: none;
        }
    }
}