.main {

    &__account {
        display: flex;
        flex-wrap: wrap;
        padding-top: 35px;
        padding-bottom: 25px;
    }
}

.profile {
    
    &__avatar {
        max-width: 224px;
        width: 100%;
        max-height: 322px;
        height: auto;
        margin-right: 15px;
        box-sizing: border-box;
        border: 1px solid #000;
        box-shadow: inset 3px 3px 0px 3px #000, inset -3px -3px 0px 3px #000;
        border-radius: 40px;
        padding-top: 30px;
        padding-bottom: 35px;
        overflow: hidden;

        &-link {
            text-decoration: none;
        }

        &-img {
            display: block;
            width: 100%;
            height: auto;
        }
    }

    &__infouser {
        max-width: 665px;
        width: 100%;
        box-sizing: border-box;
        padding: 60px 50px 65px 90px;
        background-image: url('../images/account-cloud.png');
        background-repeat: no-repeat;
    }

    &__title {
        @include text;
        margin-bottom: 10px;
        font-size: $fs-M;

        &:first-child {
            display: inline-block;
            margin-right: 15px;
        }
    }

    &__link {
        @include link;
        display: inline-block;
        position: relative;
        font-size: $fs_XS;
        font-weight: 300;

        &::before {
            content: '';
            position: absolute;
            width: 11px;
            height: 15px;
            top: 0;
            left: -13px;
            background-image: url('../images/settings.png');
            background-repeat: no-repeat;
            background-position-x: center;
        }
    }

    &__text {
        @include text;
        font-size: $fs_S;
        font-weight: 300;
        line-height: 21px;

        &:not(:last-child) {
            margin-bottom: 10px;
        }

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__msg {
        @include text;
        max-width: 371px;
        width: 100%;
        margin-bottom: 0;
        font-size: $fs-M;
        font-weight: 500;
        line-height: 26px;

        &-link {
            font-weight: 300;
        }
    }
}

@media (max-width: 943px) {
    .profile {

        &__avatar {
            margin-left: auto;
            margin-right: auto;
        }

        &__infouser {
            margin-left: auto;
            margin-right: auto;
        }
    }
}

@media (max-width: 711px) {
    .profile {
        &__avatar {
            padding-top: 25px;
            padding-bottom: 25px;
            margin-bottom: 30px;
        }

        &__infouser {
            padding: 0;
            background-image: none;
        }

        &__msg {
            margin-left: auto;
            margin-right: auto;
        }
    }

}

.portfolio {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    width: 100%;
    padding-top: 40px;
    padding-bottom: 90px;

    &__files {
        display: flex;
        flex-wrap: wrap;
    }

    &__title {
        @include title;
        width: 100%;
        margin-bottom: 30px;
        font-size: $fs_XXL;
    }

    &__folder {
        box-sizing: border-box;
        border: 1px solid transparent;
        max-width: 125px;
        width: 100%;
        height: 122px;      
        background-repeat: no-repeat;
        background-position: bottom center; 
        background-size: contain;


        &-link {
            @include link;

            &:not(:last-child) {
                margin-right: 120px;
            }
            
            &:nth-child(2) .portfolio__folder {
                background-image: url('../images/picture-folder.png');
            }
            
            &:nth-child(3) .portfolio__folder {
                background-image: url('../images/picture-folder.png');
            }

            &:nth-child(4) .portfolio__folder {
                background-image: url('../images/empty-folder.png');
            }
        }

        &-name {
            @include text;
            margin-bottom: 0;
            font-size: $fs_S;
            // text-align: center;
        }
    } 

    &__button {
        @include button;
        display: block;
        width: 40px;
        height: 40px;
        background-repeat: no-repeat;
        background-position: center;
        order: 1;

        &:first-child {
            background-image: url('../images/help.png');
        }

        &:nth-child(2) {
            background-image: url('../images/add.png');
        }

        &:nth-child(3) {
            background-image: url('../images/trash.png');
        }

        &:last-child {
            background-image: url('../images/logout.png');
        }

        &:not(:last-child) {
            margin-bottom: 40px;
        }
    }
}

@media (max-width: 711px) {
    .portfolio {
        flex-direction: column;
        align-items: center;

        &__title {
            text-align: center;
        }

        &__files {
            width: 100%;
            justify-content: space-between;
            order: 2;
        }

        &__folder {

            &-link {
                
                &:not(:last-child) {
                    margin-right: 0;
                }
            }
        }

        &__menu {
            display: flex;
            order: 1;
            margin-bottom: 50px;
        }

        &__button {           

            &:not(:last-child) {
                margin-bottom: 0;
                margin-right: 45px;
            }
        }
    }
}