@mixin list() {
    margin-top: 0;
    margin-bottom: 0;
    padding-left: 0;
    list-style-type: none;    
}

@mixin link() {
    text-decoration: none;
    color: #000;
}

@mixin title() {
    margin-top: 0;
    font-weight: bold;
    color: #000;
}

@mixin text() {
    margin-top: 0;
    color: #000;
}

@mixin button-link() {
    color: #fff;
    text-decoration: none;
    font-weight: 500;
    background-color: #000;
    box-sizing: border-box;
    border: 1px solid #000;
    border-radius: 100px;
    cursor: pointer;

    &:hover {
			box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.4);
		}
}

@mixin button() {
    box-sizing: border-box;
    background-color: transparent;
    border: none;
    padding: 0;
    cursor: pointer;
}

@mixin input() {
    box-sizing: border-box;
    width: 100%;
    border: 2px solid #000;
    background-color: transparent;
    padding-left: 20px;
    padding-top: 8px;
    padding-bottom: 8px;
    font-size: $fs-M;
    font-family: 'Roboto', sans-serif;

    &:focus {
        box-shadow: inset 1px 1px 0px 1px #000, inset -1px -1px 0px 1px #000;
        outline-color: #000;
    }

    &:invalid:not(:placeholder-shown) {
        border-color: #F84F07;
        box-shadow: inset 1px 1px 0px 1px #F84F07, inset -1px -1px 0px 1px #F84F07;
        outline-color: #F84F07;
    }
}

@mixin label() {
    font-size: $fs_S;
    font-weight: 300;
    color: #000;
}