.footer {
    max-width: 1035px;
    margin-left: auto;
    margin-right: auto;
    padding-bottom: 15px;
    background-color: #000;

    &__container {
        display: flex;
        flex-wrap: wrap;
        align-items: baseline;
        padding: 25px 65px 50px 65px;
    }

    &__logo {
        margin-right: 15px;
    }

    &__description {
        margin-top: 0;
        margin-bottom: 0;
        color: #fff;
        font-size: $fs_S;
        font-weight: 300;

        &--style {
            text-align: center;
        }
    }
}

.social {
    margin-left: auto;

    &__link {
        display: inline-block;

        &:nth-child(-n+3) {
            margin-right: 10px;
        }
    }
}

@media (max-width: 721px) {
    .footer {

        &__container {
            padding: 15px 30px 20px 30px;
        }
    }
}

@media (max-width: 655px) {
    .footer {

        &__container {
            align-items: center;
            flex-direction: column;
            justify-content: center;

        }

        &__logo {
            display: none;
        }
    }

    .social {
        margin-left: 0;
        margin-top: 40px;
    }
}

@media (max-width: 385px) {
    .footer {

        &__description {

            &--style {
                font-size: $fs_XS;
                font-weight: 500;
            }
        }
    }
}