.header {
    display: flex;
    align-items: baseline;
    justify-content: space-between;
}

@media (max-width: 710px) {
    .header {
        align-items: center;
    }
}

.nav {
    width: 100%;
    padding-left: 65px;

    &__list {
        @include list;
        display: flex;
        justify-content: center;
    }

    &__item {

        &:nth-child(-n+3) {
            margin-right: 35px;
        }

        &:nth-child(4) {
            margin-right: auto;
        }

        &:nth-child(5) {
            position: relative;
            margin-right: 15px;
        }

        &:hover .nav__svg {
            transform: rotate(180deg);
            transition: 0.5s;
            fill: #000;
        }

        &:hover .submenu__list {
            display: block;
        }
    }

    &__link {
        @include link;
        font-size: $fs-M;        

        &--mobile {
            @include link;
            display: none;
            font-size: 22px;

            &:hover {
            text-decoration: underline;
            }

        }

        &--active {
            text-decoration: underline;
        }

        &:hover {
            text-decoration: underline;
        }
    }

    &__svg {
        width: 13px;
        height: 13px;
    }
}

@media (max-width: 987px) {
	.nav {

		&__item {

			&:nth-child(4) {
				margin-right: 60px;
			}
        }
    }
}

.submenu {

    &__list {        
        @include list;
        display: none;
        position: absolute;
        top: 22px;
        left: -14px;
        box-sizing: border-box;
        padding: 10px 12px;
        border: 3px solid #000;
        border-radius: 20px;
        background-color: #fff;
        z-index: 10;
    }

    &__item {

        &:nth-child(-n+3) {
            margin-bottom: 8px;
        }
    }

    &__link {
        @include link;
        font-size: $fs-M;
        font-weight: 500;

        &:hover {
            text-decoration: underline;
        }
    }
}

@media (max-width: 710px) {
    .nav {
        position: relative;
        box-sizing: border-box;
        width: 40px;
        height: 14px;
        padding-left: 0;
        border-top: 2px solid #000;
        border-bottom: 2px solid #000;

        &::before {
            content: '';
            position: absolute;
            width: 40px;
            height: 2px;
            top: 50%;
            transform: translateY(-50%);
            background-color: #000;
        }

        &__list {
            display: none;
            position: absolute;
            top: 15px;
            left: -50px;
            box-sizing: border-box;
            padding: 20px 35px;
            border: 3px solid #000;
            border-radius: 20px;
            background-color: #FFB800;
            z-index: 10;
            text-align: center;
        }

        &__item {
            
            &:first-child {
                display: none;
            }

            &:nth-child(-n+4) {
                margin-right: 0;
                margin-bottom: 15px;
            }

            &:nth-child(4) {
                position: relative;
            }

            &:last-child {
                display: none;
            }
        }

        &__wrapper {
            padding: 12px 16px;
            background-image: url('../images/sigin-cloud.png');
            background-repeat: no-repeat;
            background-size: cover;
        }

        &__link {
            font-weight: 500;

            &--mobile {
                display: block;               
            }
        }

        &__svg {
            position: absolute;
            top: 8px;
            right: -5px;
        }

        &:hover .nav__list {
            display: block;
        }
    }

    .submenu {

        &__list {
            background-color: #FFB800;
            top: -108px;
            left: 105px;
        }
    }
}