.main {
    
    &__page404 {
        padding-bottom: 50px;
    }
}

@media (max-width: 929px) {
    .main {

        &__page404 {
            padding-top: 10px;
        }
    }
}

.page404 {

    &__img {
        display: block;
        max-width: 423px;
        width: 100%;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 40px;
    }

    &__message {
        @include text;
        margin-bottom: 40px;
        font-size: $fs-M;
        font-weight: 500;
        text-align: center;
    }

    &__link {
        @include button-link;
        display: block;
        max-width: 220px;
        padding-top: 7px;
        padding-bottom: 7px;
        font-size: $fs_L;
        text-align: center;
        margin-left: auto;
        margin-right: auto;
    }
}