.main {

    &__styles {
        padding-bottom: 25px;
    }
}

.styles {

    &__wrapper-text {     
        overflow: hidden;

        &:hover {
        overflow-y: auto;
        overflow-x: hidden;
        -webkit-overflow-scrolling: touch;
        -moz-overflow-scrolling: touch;
        -ms-overflow-scrolling: touch;
        -o-overflow-scrolling: touch;
        -ms-overflow-style: touch;
		}

		&::-webkit-scrollbar {
			width: 10px;
			border-radius: 8px;
		}

		&::-webkit-scrollbar-track {
			background-color: rgba(0, 0, 0, 0.5);
		}

		&::-webkit-scrollbar-thumb {
			background-color: #ffb800;
			border-radius: 8px;
			border: 2px solid rgba(0, 0, 0, 0.5);
		}
    }

    &__title {
        @include text;
        margin-bottom: 10px;
        font-size: $fs_XL;
        font-weight: 500;
    }

    &__text {
        @include text;
        font-size: $fs_XS;
        line-height: 24px;

        &:nth-child(2) {
            margin-bottom: 15px;
        }

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__img {
        display: block;
        width: 100%;
    }

    &__box {
        width: 100%;
        box-sizing: border-box;
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
    }
}

.section {

    &__styles {
        display: flex;
        align-items: flex-end;
        justify-content: center;

        &:not(:last-child) {
            margin-bottom: 55px;
        }

        &:first-child {

            & .styles__wrapper-text {
                height: 302px;
            }

            &> .styles__box {
                padding: 110px 100px;
                max-width: 615px;
                background-image: url('../images/styles-cloud1.png');
            }            

            &> .styles__img {
                position: relative;
                top: -75px;
                right: 31px;
                order: 1;
                width: 100%;               
                max-width: 264px;
                height: auto;   
            }
        }

        &:nth-child(2) {

            & .styles__wrapper-text {
                height: 278px;
            }

            &> .styles__box {
                padding: 100px 120px 100px 110px;
                max-width: 615px;
                background-image: url('../images/styles-cloud2.png');
            }
            
            &> .styles__img {
                position: relative;
                top: -24px;
                left: -14px;
                max-height: 385px;
                width: auto;
                transform: rotate(22deg); 
            }
        }

        &:last-child {

            & .styles__wrapper-text {
                height: 374px;
            }

            &> .styles__box {
                padding: 100px 90px 100px 120px;
                max-width: 625px;
                background-image: url('../images/styles-cloud3.png');
            }
            
            &> .styles__img {
                position: relative;
                top: -30px;
                right: 60px;
                order: 1;                
                max-width: 310px;
                height: auto;   
            }
        }
    }
}

@media (max-width: 769px) {
    .section {

        &__styles {

            &:first-child {

                &> .styles__box {
                    padding: 65px 55px 66px 100px;
                }

                &> .styles__img {
                    max-width: 200px;                    
                }               
            }

            &:nth-child(2) {

                &> .styles__box {
                    padding: 75px 85px 65px 100px;
                }

                &> .styles__img {
                    top: -50px;
                    left: 25px;
                    max-height: 300px;                    
                }
            }

            &:last-child {

                & .styles__wrapper-text {
                    height: 305px;
                }

                &> .styles__box {
                    padding: 65px 80px 70px 100px;
                }

                &> .styles__img {
                    top: -55px;
                    right: 55px;
                    max-width: 205px;                    
                }               
            }
        }
    }
}

@media (max-width: 747px) {
    .main {

        &__styles {
            padding-top: 15px;
        }
    }

    .styles {

        &__container {
            background-color: #FFB800;
        }

        &__title {
            font-size: $fs-M;
        }

        &__img {
            margin-bottom: 30px;
        }
    }

    .section {

        &__styles {
            flex-direction: column;
            align-items: center;

            &:first-child {

                &> .styles__box {
                    background-image: none;
                    padding: 0;
                }

                &> .styles__img {
                    max-width: 163px;
                    position: static;
                    order: 0;
                    padding: 5px 10px;
                    border: 4px solid #000;
                    border-radius: 40px;
                    background-color: #fff;                
                }
                
                & .styles__wrapper-text {
                    height: 100%;
                }
            }

            &:nth-child(2) {

                &> .styles__box {
                    background-image: none;
                    padding: 0;
                }

                &> .styles__img {
                    max-width: 146px;
                    position: static;
                    padding: 20px;
                    border: 4px solid #000;
                    border-radius: 40px;
                    transform: rotate(0deg);
                    background-color: #fff;               
                }

                & .styles__wrapper-text {
                    height: 100%;
                }
            }

            &:last-child {

                &> .styles__box {
                    background-image: none;
                    padding: 0;
                }

                &> .styles__img {
                    max-width: 179px;
                    position: static;
                    order: 0;
                    border: 4px solid #000;
                    border-radius: 40px;
                    padding: 10px;
                    background-color: #fff;                 
                }
                
                & .styles__wrapper-text {
                    height: 100%;
                }
            }
        }            
    }    
}

